import { React, useState, useEffect, Fragment } from "react"
import axios from "axios"
//import { Spinner } from "./components/Spinner"
import { trackPromise } from "react-promise-tracker"
import ShowSpinner from "./components/ShowSpinner"
import ShowAlert from "./components/ShowAlert"

const About = props => {
  //const { setItems, items, setDisplayAlert, mcTable, addMC, editMode, currentSU, currentMF } = props

  const [result, setResult] = useState([])
  const [alert, setAlert] = useState({ position: 100 })

  function sortInspectionDate(_result) {
    _result.sort(function (a, b) {
      // note : must change the string back to date object for comparison
      if (Date.parse(a["main"].inspection_date) > Date.parse(b["main"].inspection_date)) return -1
      else return 1
    })

    return _result
  }

  useEffect(() => {
    trackPromise(
      axios({
        url: "/api/pastXdaysResult",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        }
        //data: JSON.stringify({})   seems like this line has to be removed to avoid error from Heroku as this line is passing empty parameter.
      })
        .then(response => response.data)
        .then(response => {
          const _result = sortInspectionDate(response)
          setResult(_result)
          //console.log ('x day results', result)
        })
        .catch(error => {
          console.log(error)
        }),
      "pastInspection"
    )
    return () => {
      setResult({}) // This worked for me
    }
  }, [])

  // convert to local time
  function convertToLocalDate(date) {
    //const t = new Date(date).toTimeString()
    //return d + " " + t
    const d = new Date(date).toDateString()
    return d
  }

  // convert to local time
  function convertToLocal(date) {
    const t = new Date(date).toTimeString()
    const d = new Date(date).toDateString()
    var local = d + " " + t
    var local_end = local.search("GMT+") - 1
    local = local.substring(0, local_end)
    return local
  }

  const setDisplayAlert = alert => {
    //{ message: "hi", position: 0, timeout: 3000, color: "alert__green" }
    setAlert(alert)
    setTimeout(function () {
      setAlert({ position: 0 })
    }, alert.timeout)
  }

  const printReport = (inspectionID, submitted, qa_type, mqa) => {
    //event.preventDefault()
    if (qa_type === undefined) {
      setDisplayAlert({ message: "QA Type must be chosen !", position: 0, timeout: 2000, color: "alert__red" })
      return
    }
    if (qa_type === "MQA" && (mqa === undefined || mqa === "Select")) {
      setDisplayAlert({ message: "MQA must be specified !", position: 0, timeout: 2000, color: "alert__red" })
      return
    }

    const file_name = "MMG Inspection Report - " + inspectionID["mc"] + "-" + inspectionID["iteration"] + "-" + inspectionID["type"] + (!submitted ? " (Draft) " : "") + ".xlsx"

    trackPromise(
      fetch("/printreport", {
        method: "POST",
        headers: {
          "Content-Type": "application/octet-stream",
          "Content-Disposition": "attachment",
          "Cache-Control": "no-cache",
          inspectionID: JSON.stringify({ inspectionID }),
          localTime: JSON.stringify(convertToLocal(Date.now()))
        }
      })
        .then(response => response.blob())
        .then(blob => {
          // Create blob link to download
          const url = window.URL.createObjectURL(new Blob([blob]))
          const link = document.createElement("a")
          link.href = url
          link.setAttribute("download", file_name)

          // Append to html link element page
          document.body.appendChild(link)

          // Start download
          link.click()

          // Clean up and remove the link
          link.parentNode.removeChild(link)
        }),
      "printReport"
    )
  }

  return (
    <Fragment>
      <div className="xDaysResult">
        {result.length > 0 ? (
          <div className="xDaysResult_gridHeader">
            <div className="xDaysResult_gridHeaderItem">Inspection Date</div>
            <div className="xDaysResult_gridHeaderItem">Supplier</div>
            <div className="xDaysResult_gridHeaderItem">Manufacturer</div>
            <div className="xDaysResult_gridHeaderItem">Inspection ID</div>
            <div className="xDaysResult_gridHeaderItem">Result</div>
            <div className="xDaysResult_gridHeaderItem">Status</div>
            <div className="xDaysResult_gridHeaderItem">Download Report</div>
          </div>
        ) : null}

        {alert.position === 0 ? (
          <div className="flex-row">
            <div className="flex-large">
              <ShowAlert alert={alert} />
            </div>
          </div>
        ) : null}

        {result.length > 0
          ? result.map(item => (
              <div key={item.id} className="xDaysResult_gridDetail">
                <div className="xDaysResult_gridItem">{convertToLocalDate(item["main"].inspection_date)}</div>
                <div className="xDaysResult_gridItem">{item["main"].su_no}</div>
                <div className="xDaysResult_gridItem">{item["main"].mf_no}</div>
                <div className="xDaysResult_gridItem">{item["_id"].mc + "-" + item["_id"].iteration + "-" + item["_id"].type} </div>
                <div className="xDaysResult_gridItem">{item["misc"].final_result}</div>
                <div className="xDaysResult_gridItem">{item["misc"].submitted ? "Released" : "Draft"}</div>
                <div className="xDaysResult_gridItem">
                  <i
                    className="clickable fa fa-download"
                    onClick={() => {
                      printReport({ mc: item["_id"].mc, iteration: item["_id"].iteration, type: item["_id"].type }, item["misc"].submitted, item["misc"].qa_type, item["misc"].mqa)
                    }}
                  ></i>
                </div>
              </div>
            ))
          : null}
      </div>
      {/* <Spinner area="pastInspection" /> */}
      <ShowSpinner position={10} />
    </Fragment>
  )
}

export default About
